import React, { useEffect, useState } from "react";
import "./App.css";
import StationList, { Station } from "./StationList";
import { ThemeProvider, createTheme } from "@mui/material";

// Define your custom theme
const theme = createTheme({
  // Add your theme configuration here
});

const App: React.FC = () => {
  const [stationList, setStationList] = useState<Station[]>([]);

  useEffect(() => {
    const fetchStationList = async () => {
      try {
        const baseUrl = "https://de1.api.radio-browser.info"
        // const baseUrl = await get_radiobrowser_base_url_random();
        const response = await fetch(
          `${baseUrl}/json/stations/bycountrycodeexact/pt?order=clickcount&reverse=true`
        );
        const data = await response.json();
        setStationList(data);
      } catch (error) {
        console.log("Error fetching station list:", error);
      }
    };

    fetchStationList();
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <StationList stationList={stationList} />
    </ThemeProvider>
  );
};

export default App;
