import React, { useEffect } from "react";
import { Theme, styled } from "@mui/material/styles";
import {
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Avatar,
  Divider,
  Paper,
  IconButton,
  Box,
} from "@mui/material";
import { PlayCircleOutline } from "@mui/icons-material";
import AudioPlayer from "./AudioPlayer";

export type Station = {
  stationuuid: string;
  name: string;
  favicon: string;
  tags: string;
  url_resolved: string;
};

export type StationListProps = {
  stationList: Station[];
};

const StyledBox = styled(Box)(({ theme }: { theme: Theme }) => ({
  margin: "auto",
  marginTop: theme.spacing(2),
  maxWidth: 720,
  width: "100%",
  [theme.breakpoints.down("sm")]: {
    maxWidth: "100%",
  },
}));

const StyledList = styled(List)(({ theme }: { theme: Theme }) => ({
  backgroundColor: theme.palette.background.paper,
}));

const StyledAvatar = styled(Avatar)(({ theme }: { theme: Theme }) => ({
  width: theme.spacing(7),
  height: theme.spacing(7),
}));

const StyledAvatarContainer = styled(Paper)(({ theme }: { theme: Theme }) => ({
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  marginRight: theme.spacing(2),
  padding: theme.spacing(0.5),
  border: `2px solid ${theme.palette.primary.main}`,
}));

const StyledIconButton = styled(IconButton)(({ theme }: { theme: Theme }) => ({
  marginLeft: "auto",
  padding: theme.spacing(1.5), // Adjust the padding to increase the button size
}));

const StationList: React.FC<StationListProps> = ({ stationList }) => {
  const [currentStation, setCurrentStation] = React.useState<Station | null>(
    null
  );

  const handlePlay = (station: Station) => {
    const name = station.name.replace(/ /g, "-");
    const id = station.stationuuid;
    window.history.pushState(null, "", encodeURI(`/${id}/${name}`));
    setCurrentStation(station);
  };

  return (
    <StyledBox>
      {currentStation && <AudioPlayer selectedStation={currentStation} />}
      <StyledList>
        {stationList.map((radio, index) => (
          <React.Fragment key={radio.stationuuid}>
            <Divider />
            <ListItem>
              <ListItemAvatar>
                <StyledAvatarContainer>
                  <StyledAvatar alt={radio.name} src={radio.favicon} />
                </StyledAvatarContainer>
              </ListItemAvatar>
              <ListItemText primary={radio.name} secondary={radio.tags} />
              <StyledIconButton
                color="primary"
                aria-label="Play"
                onClick={() => handlePlay(radio)}
                style={{ cursor: "pointer" }}
              >
                <PlayCircleOutline fontSize="large" color="primary" />
              </StyledIconButton>
            </ListItem>
          </React.Fragment>
        ))}
      </StyledList>
    </StyledBox>
  );
};

export default StationList;
